import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { ModalPage } from '../modal/modal.page';
import { HttpService } from '../service/http.service';
import * as moment from "moment";

@Component({
  selector: 'app-telefones',
  templateUrl: './telefones.page.html',
  styleUrls: ['./telefones.page.scss'],
})
export class TelefonesPage implements OnInit {
  private endereco: string;
  private total: number;
  private telefonesOriginal: any[];
  private telefonesFiltered: any[];
  private filterOption: string;

  constructor(
    private route: ActivatedRoute, 
    private http: HttpService,
    private modalController: ModalController,
  ) { }

  async ngOnInit() {
    moment.locale('pt-br');
    this.endereco =  this.route.snapshot.paramMap.get('endereco');
    const res = await this.http.getAllPhonesByAddress(this.endereco);
    this.total = res[0].total;
    this.telefonesOriginal = res[0].telefones;

    this.endereco = `${this.endereco} - Total: ${this.total}`;

    this.telefonesOriginal = this.telefonesOriginal.map(telefone => {
      telefone.ultimaVezConsultado = telefone.ultimaVezConsultado != '' ? moment(telefone.ultimaVezConsultado).fromNow() : 'Nunca';
      return telefone;
    })

    this.telefonesFiltered = this.telefonesOriginal;

  }

  go(item) {
    window.open(`tel:${item.telefone}`, '_system');
  }

  filter() {
    this.telefonesFiltered = this.telefonesOriginal.filter(t => {
      return this.filterOption === 'y' ?
       (t.ultimaVezConsultado !== 'Nunca') :
       (t.ultimaVezConsultado === 'Nunca');
    });
  }

  async presentModal(telefone) {
    const modal = await this.modalController.create({
      component: ModalPage,
      componentProps: {
        'telefone': telefone
      }
    });
    return await modal.present();
  }
}
